import {commumal,Applyer,addition} from "../public"
var addReimburse={
    passValue:"",
    dateId:"",
    typeId:"",
    leiXingId:"",
    area:null,//记录地区费用
    dateTime:"",
    typeContent:"",
    typeData:null,//类型数据
    type:"",//记录是出差还是招待，1为招待，2为出差
    methods:{
        init(){
            $(".btnBack").on('click',commumal.back);
            $("#verification").on("change",addReimburse.events.isMoney)
            $(".SelectedDate").on('click',addReimburse.events.appendTime)
            $(".applyUser").on("click",addReimburse.events.applyUser)
            $(".payee").on("click",addReimburse.events.payee)
            $(".yongtu").on('click',addReimburse.events.yongtu)
            $(".leixing").on('click',addReimburse.events.leiXing)
            $(".receipts .choose").on('click',addReimburse.events.receipt)
            $(".receipts span.delete").on('click',addReimburse.events.clear)
            $(".trip input").on('change',addReimburse.events.estimate)
            $(".tripNumber input").on('change',addReimburse.events.estimate)
            $(".guestNumber input").on('change',addReimburse.events.estimate)
            $(".areaSelect").on("change",addReimburse.events.estimate)
            $(".query .weui-cell_access").on("click",addReimburse.events.mylist);
            $(".footerBtn .submit").on("click",addReimburse.events.submit)
            $('#uploaderCustomFiles').on('click',commumal.onShowImg);
        }
    },
    events:{
        loadData(param){
            $.defs.loadingshow();
             $.defs.myPost({
                SP_NAME: 'SP_RBRB_PRE_LOAD',
                m: 'multi_result',
                RBRB_KY: param? param.content.RBRB_KY : 0
             }).then((ref)=>{
                 $.defs.loadinghide();
                 $("nav .billId").html(ref.bill_id.RBRB_ID);
                 if (ref.result.RETURN_CODE != 0) {
                    $("#view").html("");
                     return;
                 }
                 addReimburse.config.initdataSave = ref;
                 if(param){
                     let {rbrb_info:rbrbInfo,rbrb_images:rbrbImg}=ref;
                    addReimburse.dateTime=rbrbInfo.RBRB_APPLY_DT.split("-");
                    addReimburse.typeContent=[rbrbInfo.PARENT_FEPP_KY,rbrbInfo.FEPP_KY];
                    addReimburse.events.fillData(rbrbInfo);
                    addReimburse.config.fillImage(rbrbImg)
                    addReimburse.events.DropdownList()
                    commumal.uploader=rbrbImg.length;
                 }

             })
        },
        purpose(data){
            let listData=[];
             let {finance_fee_type:list}=data;
            for(let i=0;i<list.length;i++){
                let arr=[];
                list[i].label=list[i].FEPP_NAME;
                list[i].value=list[i].FEPP_KY;
                for(let j=0;j<list.length;j++){
                    if(list[i].FEPP_KY==list[j].PARENT_FEPP_KY){
                        arr.push(list[j])
                    }
                }
                if(arr.length){
                    list[i].children=arr;
                    listData.push(list[i])
                }
            }
            return listData
         },
         DropdownList(){
             $.defs.myPost({
                m:"SELECT",
                SP_NAME:"TB_BSTR_COST_DROPDOWN_LIST",
                DPPS_KY:$('.applyUser').attr('data-value')
             }).then((data)=>{
                data.unshift({
                    BSTR_ID:"",
                    BSTR_NAME:"请选择",
                    BSTR_DESC:""
                })
                addReimburse.area=data
                if(addReimburse.passValue){
                    addReimburse.events.estimate()
                }
             })
         },
         appendTime(){
             commumal.datePicker({
                   dateTime: addReimburse.dateTime,
                   confirm(result){
                     result[1]=result[1]-1;
                     $(".SelectedDate span").html(moment(result).format("YYYY-MM-DD"));
                     addReimburse.events.cancelReimburse();
                     $('.receipts').attr('atdc_ky', '').find('span.choose').text('请选择');
                      addReimburse.events.costMode()
                   },
                   id: addReimburse.dateId
             })
         },
         applyUser(){
            Applyer.employee({
                span:(self)=>{
                    var $item=$(self);
                    var ky = $item.attr('apps');
                    $('.applyUser').attr('data-value', ky).find('span').text($item.html());
                    $(".payee").attr('data-value', ky).find('span').text($item.html());
                    addReimburse.events.cancelReimburse();
                    $('.receipts').attr('atdc_ky', '').find('span.choose').text('请选择');
                    addReimburse.events.costMode();
                    $.hidebox();
                }
            })
         },
         cancelReimburse(){
            if($('.receipts').find("span.choose").text()!="请选择"){
                $.defs.msg("附加单据已清空")
            }
        },
        costMode(){
            if($(".receipts .choose").text()=="请选择"){
                $(".baobei").addClass('dn');
                $(".receipts").find("span.delete").addClass("dn")
            }else{
                $(".baobei").removeClass('dn');
                let {hr_notes_area:arr}=JSON.parse(commumal.caching('BASE_INFO'))
                arr.unshift({
                    BSTR_ID:"",
                    BSTR_NAME:"请选择",
                    BSTR_DESC:""
                })
                arr.forEach(item=>{
                    if(item.BSTR_DESC){
                        item.BSTR_NAME=item.BSTR_NAME+"("+item.BSTR_DESC+")"
                    }else{
                        item.BSTR_NAME=item.BSTR_NAME
                    }
                })
                var areaArr=$.defs.selectOption(arr,"BSTR_ID","BSTR_NAME")
                $(".areaSelect").html(areaArr);

            }
        },
        payee(){
            Applyer.employee({
                span:(self)=>{
                     let $item=$(self);
                     var ky=$item.attr("apps")
                     $(".payee").attr('data-value', ky).find('span').text($item.html());
                     $.hidebox()
                }
            }) 
        },
        yongtu(){
            let data=JSON.parse(commumal.caching('BASE_INFO'));
            let usedata=addReimburse.events.purpose(data);
            var that=this;
            commumal.reimbursePicker({
                 data:usedata,
                 default:addReimburse.typeContent==""?[100]:[addReimburse.typeContent[0]],
                 confirm(result){
                    $(that).find("span").text(result[0].FEPP_NAME)
                    $(that).attr("PARENT_FEPP_KY",result[0].FEPP_KY)
                    addReimburse.typeData=result[0].children;
                    addReimburse.leiXingId="g"+Math.random().toString();
                    addReimburse.typeContent="";//去缓存并改变类型得默认值
                    $(".leixing").attr({
                        "FEPP_KY":addReimburse.typeData[0].FEPP_KY,
                        "sysv":addReimburse.typeData[0].FORMULA
                    }).find("span").text(addReimburse.typeData[0].FEPP_NAME)
                    if($(".choose").text()!="请选择"){
                        addReimburse.events.estimate()
                    }
                 },
                 id:addReimburse.typeId
            })
        },
        leiXing(){
            let data=addReimburse.typeData;
            var that=this;
            commumal.reimbursePicker({
                data:data,
                default:addReimburse.typeContent==""?[data[0].FEPP_KY]:[addReimburse.typeContent[1]],
                confirm(result){
                    $(that).find("span").text(result[0].FEPP_NAME)
                    $(that).attr("FEPP_KY",result[0].FEPP_KY)
                    $(that).attr("sysv",result[0].FORMULA)
                    if($(".choose").text()!="请选择"){
                       addReimburse.events.estimate()
                    }
                },
                id:addReimburse.leiXingId
            })
        },
        receipt(){
            addition.bills({
                span:(self)=>{
                    let $item=$(self)
                    var ky = $item.attr('ATDC_KY');
                    $('.receipts').attr('ATDC_KY', ky).find('span.choose').text($item.find('.cardNumber').text() + '-' + $item.find('.cardType').text());
                    $(".receipts span.delete").removeClass("dn");
                    addReimburse.events.reportClick(addition.list,ky)
                }
            })
        },
        reportClick(ref,ky){
            addReimburse.events.costMode();
            var selectData=ref.find(item=>{
                return item.ATDC_KY==ky
            })
            $(".baobei").removeClass('dn');
            if(selectData.SYSV_ATDC_TYPE=="BSTP"){
                $(".guestNumber").addClass('dn');
                $(".trip").removeClass("dn");
                $(".tripNumber").removeClass("dn");
                addReimburse.type=2;
                $(".trip input").val(selectData.ATDC_DAY)
                $(".tripNumber input").val(selectData.ATDC_HUMAN_CNT)
            }else{
                $(".trip").addClass("dn");
                $(".tripNumber").addClass('dn');
                $(".guestNumber").removeClass("dn")
                addReimburse.type=1
                $(".guestNumber input").val(selectData.ATDC_HUMAN_CNT)
            }
            if(selectData.BSTR_ID){
                $(".areaSelect").val(selectData.BSTR_ID)
            }
            $(".baobeiTime").html(selectData.ATDC_START_DT)
            $(".detailsAddress").val(selectData.ATDC_SITE)
            addReimburse.events.estimate();
            $.hidebox();
        },
        clear(){
            $(".receipts span.choose").text("请选择");
            addReimburse.events.costMode();
            $(".receipts").attr("atdc_ky","")
            $(this).addClass("dn")
        },
        estimate(){
            let  tripDay=$(".trip input").val();
            let choose=$(".receipts .choose").text()
            let sysv=$(".leixing").attr('sysv');
            let bstr_id=$(".areaSelect").val();
            if(bstr_id){
                let selectData=addReimburse.area.find(item=>{
                    return item.BSTR_ID==bstr_id
                })
                if(sysv&&choose!="请选择"){
                    var numb=$("div.number:not(.dn)").find("input").val();
                    if(isNaN(parseFloat(numb))){
                        $.defs.msg("人数输入有误");
                        $("div.number:not(.dn)").find("input").val(1);
                        addReimburse.events.estimate()
                        return
                    }
                    if(addReimburse.type=="2"){
                        if(isNaN(parseFloat(tripDay))|| parseFloat(tripDay)<=0){
                            $.defs.msg("天数输入有误");
                            $(".trip input").val(1);
                        }
                    }
                    var rc1=template.render(sysv,{
                        LIMIT_AC:selectData.LIMIT_AC,
                        LIMIT_BH:selectData.LIMIT_BH,
                        LIMIT_TM:selectData.LIMIT_TM,
                        ATDC_DAY:addReimburse.type==1?1:tripDay,
                        ATDC_HUMAN_CNT:numb
                    })
                    var  preturn=eval(rc1);
                    $(".cost").html(preturn)
                }else{
                    $('.cost').html("")
                }
            }
        },
        mylist(){
            spspa.go("/mylist")
        },
        submit(){
             var arr=[];
             let param=$.defs.submit({container: '.addReport'});
             param.FEPP_KY = $('.leixing').attr('FEPP_KY');
             param.PARENT_FEPP_KY=$(".yongtu").attr("PARENT_FEPP_KY");
             param.LINK_ATDC_KY = $('.receipts').attr('atdc_ky');
             param.APPLY_EHUSER = $('.applyUser').attr('data-value');
             param.PAYEE_KY=$(".payee").attr("data-value");
             param.SP_NAME = 'SP_RBRB_REIMBURSE_INFO_INSERT';
             param.RBRB_KY=addReimburse.config.initdataSave.rbrb_info.RBRB_KY;
             param.m = 'INSERT';
             if(isNaN(parseFloat(param.RBRB_APPLY_AMT)) || parseFloat(param.RBRB_APPLY_AMT)<=0){
                $.defs.msg("费用必须为数字且大于0");
                return;
             }
             if(addReimburse.type==2){
                param.ATDC_HUMAN_CNT=$(".tripNumber input").val()
             }
             var urlparam=addReimburse.passValue;
             if(urlparam){
                param.SP_NAME = 'SP_RBRB_REIMBURSE_INFO_UPDATE';
                param.m = 'UPDATE';
                param.RBRB_KY = addReimburse.config.getRBRB_KY;
                //第三组数据
                var lstRbim = [];
                $("#uploaderCustomFiles> li[imim_ky]").each(function (index, li) {
                    lstRbim.push($(li).attr("imim_ky"));
                });
                var rbim_ky_string = lstRbim.join(",");
                arr.push({
                    SP_NAME: "SP_IMIM_IMAGE_INFO_BATCH_UPDATE",
                    m: 'UPDATE',
                    IMIM_KY_STRING: rbim_ky_string,
                    SYS_KY: addReimburse.config.initdataSave.rbrb_info.RBRB_KY,
                    SYS_KY_TYPE:"RBRB",
                })
             }
             if(param.LINK_ATDC_KY){
                 //第二组数据
                 if(isNaN(parseFloat(param.ATDC_HUMAN_CNT))||param.ATDC_HUMAN_CNT!=parseInt(param.ATDC_HUMAN_CNT)||parseInt(param.ATDC_HUMAN_CNT)<=0){
                    $.defs.msg("人数必须是正整数");
                   return;
                }
                  let bstr_id=$(".areaSelect").val();
                  if(bstr_id){
                    arr.push({
                        m:"UPDATE",
                        SP_NAME:"SP_ATDC_ATTENDANCE_INFO_LAST_UPDATE",
                        ATDC_KY:param.LINK_ATDC_KY,
                        LAST_ATDC_DAY:param.ATDC_DAY,
                        LAST_ATDC_HUMAN_CNT:param.ATDC_HUMAN_CNT,
                        BSTR_ID:bstr_id,
                        ATDC_SITE:$(".detailsAddress").val()
                    })
                  }else{
                      $.defs.msg("地区范围不能为空");
                      return;
                  }
             }
             arr.push(param);
                addReimburse.events.confirmSubmit(arr)
             
        },
        confirmSubmit(arr){
            commumal.confirm({
                click:()=>{
                    addReimburse.events.submitEnd(arr)
                }
            })
        },
        submitEnd(arr){
            $.defs.loadingshow();
            var li = $('#uploaderCustomFiles').find('li');
            $.defs.myPost({
                m: "batch_update_transation",
                SP_NAME: "dsadsa",
                DETAIL_STRING: JSON.stringify(arr)
            }).then((data)=>{
                $.defs.loadinghide();
                if(data.RETURN_CODE==0){
                    if(li.length!=0){
                        if(!addReimburse.passValue){
                            addReimburse.config.getRBRB_KY = data.RBRB_KY;
                        }
                        addReimburse.config.SaveUpLoadImg()
                    }else{
                        if(addReimburse.passValue){
                            weui.toast("操作成功",2500)
                            setTimeout(()=>{
                              history.go(-1)
                            },2600)
                             
                        }else{
                            weui.toast("操作成功",2500)
                            setTimeout(function () {
                                spspa.go()
                             }, 2600);
                        }
                    }
                }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                }
            })
        },
        fillData(param){
            let data=JSON.parse(commumal.caching('BASE_INFO'));
            if(param.ATDC_ID){
                $('.receipts').attr('atdc_ky', param.LINK_ATDC_KY).find('span.choose').text(param.ATDC_ID + '-' + param.SYSV_ATDC_TYPE_DESC);
                $(".receipts .delete").removeClass("dn");
                addReimburse.events.costMode();
                if(param.SYSV_ATDC_TYPE_DESC=="出差"){
                    $(".guestNumber").addClass("dn");
                    addReimburse.type=2;
                }else{
                    $(".trip").addClass("dn");
                    $(".tripNumber").addClass("dn")
                    addReimburse.type=1
                }
                $(".areaSelect").val(param.BSTR_ID)
            }
            $.defs.fillData({
                container: '.addReport',
                data:param
            })
            addReimburse.config.getRBRB_KY=param.RBRB_KY;
            $(".yongtu").attr("PARENT_FEPP_KY",param.PARENT_FEPP_KY).find("span").text(param.PURPOSE);
            let fepp_ky=param.FEPP_KY;
            let parKy=param.PARENT_FEPP_KY;
            let father=addReimburse.events.purpose(data);
            let child=father.find(item=>{
                return  parKy==item.FEPP_KY
            })
            addReimburse.typeData=child.children;
            let eachData=child.children.find(item=>{
                return item.FEPP_KY==fepp_ky
            })
            $(".leixing").attr({
                "FEPP_KY":eachData.FEPP_KY,
                "sysv":eachData.FORMULA
               }).find("span").text(eachData.FEPP_NAME);
            $('.applyUser').attr('data-value', param.APPLY_EHUSER).find('span').text(param.MEME_NAME + '(' + param.DPDP_NAME+")");
            $('.payee').attr('data-value', param.PAYEE_KY).find('span').text(param.PAYEE_KY_DESC + '(' + param.PAYEE_DPDP_KY_DESC+")");
        },
        isMoney(){
            let that=$(this);
            let val=that.val();
            if(isNaN(parseFloat(val))){
                that.val("");
                $.defs.msg("金额输入有误，请重新输入")
            }else{
                that.val(parseFloat(val).toFixed(2))
            }
        }
         
    },
    config:{
        init(){
            var param =spspa.getMessage('param');
            addReimburse.passValue=param;
            addReimburse.dateId="d"+Math.random().toString();
            addReimburse.typeId="c"+Math.random().toString();
            addReimburse.leiXingId="f"+Math.random().toString();
            let data=JSON.parse(commumal.caching('BASE_INFO'));
            if(commumal.isEmptyObj(param)){
                $(".top .huan").text("报销管理>新增");
                $(".SelectedDate span").html(moment().format("YYYY-MM-DD"));
                addReimburse.events.loadData();
                let type=addReimburse.events.purpose(data);
                addReimburse.typeData=type[0].children;
                $(".leixing").attr({
                    "FEPP_KY":type[0].children[0].FEPP_KY,
                    "sysv":type[0].children[0].FORMULA
                })
                $(".applyUser").attr('data-value', data.meme_info.DPPS_KY).find('span').text(data.meme_info.MEME_NAME + '(' + data.meme_info.DPDP_NAME+")");
                $(".payee").attr('data-value', data.meme_info.DPPS_KY).find('span').text(data.meme_info.MEME_NAME + '(' + data.meme_info.DPDP_NAME+")");
                addReimburse.events.DropdownList()
            }else{
                $(".top .huan").text("报销管理>修改");
                addReimburse.events.loadData(addReimburse.passValue);
            }
            spspa.clearMessage();
            commumal.picture({
                ids:"#uploaderCustom",
                queued:function () {
                    addReimburse.config.uploadCustomFileList.push(this);
                },
                beforeSend:function (data,header) {
                    data.SYS_KY = addReimburse.config.getRBRB_KY;
                    data.SYS_KY_TYPE="RBRB"
                },
                success:function (ret) {
                    addReimburse.config.upLoadImg--;
                    if (addReimburse.config.upLoadImg == 0) {
                         weui.toast('上传成功', 2500);
                        if (commumal.isEmptyObj(addReimburse.passValue)) {
                            setTimeout(function () {
                                location.reload();
                            },2600);
                        } else {
                            window.history.back();
                        }
                    }
                }
            })

        },
        initdataSave:null,
        getRBRB_KY:null,
        uploadCustomFileList: [],
        upLoadImg: 0,
        SaveUpLoadImg(){
            var li = $('#uploaderCustomFiles').find('li[data-id]');
            var listdata = addReimburse.config.uploadCustomFileList;
            addReimburse.config.upLoadImg = li.length;
            listdata.forEach(function (file) {
                $.each(li, function (i, v) {
                    if (file.id == $(v).attr('data-id')) {
                        file.upload();
                    }
                });
            });
            if(addReimburse.passValue){
                if(addReimburse.config.upLoadImg==0){
                    weui.toast('操作成功', 3000);
                    history.go(-1)
                }
            }
        },
        fillImage(data){
            let html='';
            $.each(data,(i,v)=>{
                 html+=`
                 <li class='weui-uploader__file' imim_ky='${v.RBIM_KY}' rbrb_ky='${v.RBRB_KY}' style='background-image:url("../file/Transmit?fileName=${commumal.urlencode(v.RBIM_NAME_PATH)}")'></li>
                 `
            })
            $("#uploaderCustomFiles").append(html);
         
        }
    }
}
$(function(){
    addReimburse.config.init();
    addReimburse.methods.init();
})


